@import "../../common/styles/variables";

.header {
  background-color: #1d2f38;
  height: 55px;
  margin-bottom: 25px;
  @media (max-width: 576px) {
    height: 110px;
  }
  .headerContainer {
    display: flex;
    justify-content: space-between;
    max-width: 1100px;
    padding: 0 15px;
    height: 55px;
    box-sizing: content-box;
    margin: 0 auto;
    @media (max-width: 576px) {
      display: flex;
      flex-direction: column;
    }
    .profile {
      padding: 15px;
      color: white;
      border-right: 2px solid #33434b;
      @media (max-width: 576px) {
        position: absolute;
       top: 0;
        right: 0;
      }
      .logoutBtn {
        background: none;
        color: inherit;
        border: none;
        font: inherit;
        cursor: pointer;
        outline: inherit;
      }

      .profileBox {
        display: flex;
        align-items: center;
        gap: 8px;

        .login {
          //padding-bottom: 5px;
        }

      }
    }
  }

  .loaderLine {
    width: 100%;
    height: 6px;
    position: relative;
    overflow: hidden;
    background-color: #ddd;
    margin: 0 auto;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    border-radius: 20px;
    @media (max-width: 576px) {
      position: absolute;
      top: 110px;
    }

    &:before {
      content: "";
      position: absolute;
      left: -50%;
      height: 6px;
      width: 40%;
      background-color: coral;
      -webkit-animation: lineAnim 1s linear infinite;
      -moz-animation: lineAnim 1s linear infinite;
      animation: lineAnim 1.2s linear infinite;
      -webkit-border-radius: 20px;
      -moz-border-radius: 20px;
      border-radius: 20px;
    }

    @keyframes lineAnim {
      0% {
        left: -40%;
      }
      50% {
        left: 40%;
        width: 80%;
      }
      100% {
        left: 100%;
        width: 100%;
      }
    }
  }
}

.header__img img {
  max-width: 50px;
  max-height: 100%;
  padding: 10px;
}

.nav {
  margin-left: auto;
  display: flex;
  gap: 5px;
  align-items: center;
  border-right: 2px solid #33434b;
  padding-right: 20px;
  @media (max-width: 576px) {
    border-right: none;
  }
  & > a {
    transition: all 0.2s ease-out;
  }

  & > a:hover {
    border-bottom: 2px solid gold;
  }

  & > a:not(:nth-child(3)) {
    padding: 17px 10px 15px 10px;
  }

  & > a:nth-child(3) {
    padding: 12px 10px 9px 10px;
  }
}

.nav li {
  list-style-type: none;
}


.item {
  display: flex;
  align-items: center;
  gap: 5px;
  padding: 16px;
  color: white;
  text-decoration: none;
  .linkName {
    @media (max-width: 992px) {
      display: none;
    }
  }
}


.activeItem {
  border-bottom: 2px solid gold;
}

.notActive {
  border-bottom: 2px solid transparent;
}

